/* file for variables, colors, mixins*/

// main background-color
$main-bg-color: rgb(255, 255, 255);
//top-nav bg.color
$nav-bg-color: white;
//footer heading color
$footer-heading-color: rgb(255, 255, 255);
//footer color
$footer-color: #000000;
//theme colors
$primary: #326dee;
$secondary: #1ac888;
$error: #d32752;
$info: #f6c31c;
// color palette
$colors: ( "primary": $primary, "secondary": $secondary, "error": $error, "info": $info, "blue": #1919e6, "red": #e61919, "yellow": #e6e619, "green": #19e635, "orange": #ffa600, "purple": #9900ff, "gray": #808080, "black": #000000, "white": #ffff, );
//interact with the color map
// spacing 
$base-padding: 0.75rem;
$base-margin: 0.75rem;
// borders
$base-border-thickness: 1px;
$base-border-radius: 20px;
// box-shadow
$base-box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
// font sizes
$base-font-size: 1rem;
$font-size-sm: $base-font-size * 0.75;
$font-size-lg: $base-font-size * 1.5;
$font-size-xl: $base-font-size * 2;
$font-size-xxl: $base-font-size * 3;